import React from 'react'
import {AboutContainer, List, ListItem, Title, Typography} from "./about.styles";

const About = () => {
    return(
        <AboutContainer>
            <Title className='text-center'>ABOUT US</Title>
            <List>
                <ListItem>
                    <Title> How The Website Work </Title>
                    <List>
                        <ListItem>
                            <Typography>
                                You pick the products you want from the selections, add them to your cart, log in or sign up, and then you can simply order them.
                            </Typography>
                        </ListItem>
                    </List>
                </ListItem>
            </List>
        </AboutContainer>
    )
}

export default About