import React, {Fragment} from "react";
import {CheckoutHeaderContainer, CheckoutPageContainer} from "../checkout/checkout.styles";
import {useSelector} from "react-redux";
import {Column, Row} from "../order/order.styles";
import {Divider, HeaderBlockContainer, OrderHeaderContainer, OrderPageContainer} from "./view-order.styles";


const ViewOrders = () => {
    const {orders} = useSelector(store => store.order);
    const {currentUser} = useSelector(store => store.user);

    const myOrders = orders.filter(order => order.userId === currentUser.id);

    console.log(myOrders);

    return (
        <OrderPageContainer>
            <OrderHeaderContainer borderBottom>
                <HeaderBlockContainer width='15' uppercase>
                    <span> Orders Id </span>
                </HeaderBlockContainer>
                <HeaderBlockContainer width='15' uppercase>
                    <span> Address </span>
                </HeaderBlockContainer>
                <HeaderBlockContainer width='15' uppercase>
                    <span> Items </span>
                </HeaderBlockContainer>
                <HeaderBlockContainer width='15' uppercase>
                    <span> Total Price </span>
                </HeaderBlockContainer>
            </OrderHeaderContainer>
            <Divider/>
            {
                myOrders.map((order) => {
                    return(
                        <Fragment>
                            <OrderHeaderContainer key={order.id} borderBottom my={5}>
                                <HeaderBlockContainer width='15'>
                                    <span> {order.id} </span>
                                </HeaderBlockContainer>

                                <HeaderBlockContainer width='15'>
                                    <span> {order.address} </span>
                                </HeaderBlockContainer>

                                <HeaderBlockContainer width='15'>
                                    {
                                        order?.items.map(item => (
                                            <Fragment key={item.name}>
                                                <span> {item.quantity} </span>
                                                <span> {item.name} </span>
                                                <br/>
                                            </Fragment>
                                        ))}
                                </HeaderBlockContainer>

                                <HeaderBlockContainer width='15'>
                                    <span> $ {order.totalPrice.toFixed(2)} </span>
                                </HeaderBlockContainer>
                            </OrderHeaderContainer>
                            <Divider/>
                        </Fragment>
                    )
                })
            }
        </OrderPageContainer>
    )
}

export default ViewOrders