import React, {useState} from "react"
import FormInput from "../../components/form-input/form-input.component";
import CustomButton from "../../components/custom-button/custom-button.component";
import {ShippingContainer, ShippingTitle} from "./shipping.styles";
import {useSelector} from "react-redux";
import {FooterBlockContainer} from "../checkout/checkout.styles";
import {v4 as uuid} from "uuid"
import {useHistory} from "react-router-dom";

const Shipping = () => {
    const history = useHistory()
    const {currentUser} = useSelector(store => store.user);
    const {cartItems} = useSelector(store => store.cart);

    const [orderDetails, setOrderDetails] = useState({
        id: uuid(),
        name: currentUser.displayName,
        email: currentUser.email,
        city: '',
        phone: '',
        postalCode: '',
        country: '',
        address: '',
        userId: currentUser.id
    });

    const handleChange = (e) => setOrderDetails({...orderDetails, [e.target.name]: e.target.value});
    const handleSubmit = (e) => {
        e.preventDefault();
        history.push({pathname: '/order', state: orderDetails})
    }

    return (
        <ShippingContainer>
            <ShippingTitle>Shipping</ShippingTitle>
            <span>Please fill the required fields</span>
            <form onSubmit={handleSubmit}>
                <FormInput
                    name='name'
                    label='Name'
                    type='text'
                    value={currentUser?.displayName}
                    onChange={handleChange}
                    required
                />

                <FormInput
                    name='email'
                    label='Email'
                    type='email'
                    value={currentUser?.email}
                    onChange={handleChange}
                    required
                />
                <FormInput
                    name='phone'
                    label='Phone/Mobile Number'
                    type='text'
                    value={orderDetails.phone}
                    onChange={handleChange}
                    required
                />
                <FormInput
                    name='city'
                    label='City'
                    type='text'
                    value={orderDetails.city}
                    onChange={handleChange}
                    required
                />
                <FormInput
                    name='postalCode'
                    label='Postal Code'
                    type='text'
                    value={orderDetails.postalCode}
                    onChange={handleChange}
                    required
                />
                <FormInput
                    name='country'
                    label='Country'
                    type='text'
                    value={orderDetails.country}
                    onChange={handleChange}
                    required
                />
                <textarea name='address' placeholder='Address' value={orderDetails.address} onChange={handleChange}
                          rows={3} style={{width: '100%'}}>
                </textarea>
                <FooterBlockContainer>
                    <CustomButton type='submit'> Continue </CustomButton>
                </FooterBlockContainer>
            </form>
        </ShippingContainer>
    )
};

export default Shipping